html {
  scroll-behavior: smooth;
}
.nav {
  display: flex;
  margin-bottom: 220px;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}

.nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  gap: 20px;
}

.nav ul li {
  margin: 0;
}

.nav ul li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  transition: color 0.3s;
}

.nav ul li a:hover {
  color: #ffea9f;
}

.hero {
  margin-top: 50px;
  margin-bottom: 230px;
  display: flex;
  flex-direction: column;
  align-items: left;
  text-align: left;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.hero p {
  line-height: 1.5;
  margin-bottom: 20px;
  color: white;
}
.profile-pic {
  border-radius: 50%;
  width: 100px;
  height: 100px;
}
.buttons {
  display: flex;
  justify-content: flex-start;
  gap: 15px;
  width: 100%;
}
.button {
  background-color: #333;
  color: white;
  text-decoration: none;
  border-radius: 25px;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: background-color 0.3s;
  font-size: 16px;
}
.button i {
  font-size: 20px;
}
.button:hover {
  background-color: #555;
}
@font-face {
  font-family: 'Poppins';
  src: url('../fonts/Poppins-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Poppins';
  src: url('../fonts/Poppins-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}
@media (max-width: 768px) {
  .buttons {
    display: flex;
    flex-wrap: wrap; 
    gap: 10px;
  }

  .button:last-child {
    margin-top: 10px; 
  }
}
.nav ul li a {
  font-family: 'Poppins', sans-serif;
  color: white;
  text-decoration: none;
  font-weight: 200;
}
.hero h1 {
  font-family: 'Poppins', sans-serif;
  font-weight: 550;
  margin-bottom: 10px;
  width: 100%;
  color: white;
}
.highlight-yellow {
  color: #ffea9f;
}
.highlight-pink {
  color: #ffbbc3;
}
.highlight-blue {
  color: #b6fbff;
}
.foto {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
}
